import { get, isEmpty } from 'lodash';
import FieldNormalizer from 'field-normalizer';

import Api from '../utils/api';

const Tickets = {
  getTickets(params) {
    return Api.get('/tickets', {
      location_id: get(params, 'locationId'),
      assignee_id: get(params, 'assigneeId'),
      category_id: get(params, 'categoryId'),
      ticket_tag_id: get(params, 'ticketTagId'),
      deadline: get(params, 'deadline'),
      priority: get(params, 'priority'),
      archived: get(params, 'archived'),
      status: get(params, 'status'),
      per_page: get(params, 'perPage'),
      page: get(params, 'page')
    });
  },
  getTicket(ticketId) {
    if (ticketId) {
      return Api.get(`/tickets/${ticketId}`);
    }
    return null;
  },
  getTicketByCode(code) {
    if (code) {
      return Api.get(`/tickets/codes/${code}`);
    }
    return null;
  },
  updateTicket({ ticket, selectedAssignees, selectedTags, followerIds }) {
    const body = {
      id: get(ticket, 'id'),
      title: get(ticket, 'title'),
      priority: isEmpty(get(ticket, 'priority')) ? null : get(ticket, 'priority'),
      deadline: get(ticket, 'deadline'),
      location_id: get(ticket, 'location.id'),
      assignee_ids: selectedAssignees,
      ticket_tag_ids: selectedTags,
      category_id: get(ticket, 'category.id')
    };
    if (followerIds) body.follower_ids = followerIds;

    return Api.put(`/tickets/${get(ticket, 'id')}`, body);
  },
  moveTicket({ id, status, position, location_id }) {
    return Api.put(`/tickets/${id}/move`, { status, position, location_id });
  },
  archiveTicket({ ticketsIds }) {
    return Api.put('/tickets/archive', { tickets_ids: ticketsIds });
  },
  unarchiveTicket({ ticketsIds }) {
    return Api.put('/tickets/unarchive', { tickets_ids: ticketsIds });
  },
  postTicket({ attachment_data: receivedAttachmentData, ...otherAttrs }) {
    let attachmentData = null;
    if (receivedAttachmentData) {
      attachmentData = {
        id: receivedAttachmentData.id,
        storage: receivedAttachmentData.storage,
        metadata: receivedAttachmentData.metadata,
        base64Data: receivedAttachmentData.base64Data
      };
    }
    return Api.post('/tickets', {
      ...otherAttrs,
      attachment_data: attachmentData,
      priority: isEmpty(get(otherAttrs, 'priority')) ? null : get(otherAttrs, 'priority')
    });
  },
  deleteTicket(ticketId) {
    return Api.delete(`/tickets/${ticketId}`);
  },
  getAssignables() {
    return Api.get('/tickets/assignables');
  },
  exportCsv({ start_date, end_date }) {
    return Api.post('/tickets/export_csv', { start_date, end_date });
  },
  report({ location_id, start_date, end_date }) {
    return Api.post('/tickets/report', { location_id, start_date, end_date });
  },
  markAsRead(ticketId) {
    return Api.put(`/tickets/${ticketId}/mark_as_read`);
  },
  postPublicTicket({
    ticket: { attachment_data: receivedAttachmentData, ...otherAttrs },
    companyId
  }) {
    let attachmentData = null;
    if (receivedAttachmentData) {
      attachmentData = {
        id: receivedAttachmentData.id,
        storage: receivedAttachmentData.storage,
        metadata: receivedAttachmentData.metadata,
        base64Data: receivedAttachmentData.base64Data
      };
    }
    return Api.post(
      `/public_tickets?company_id=${companyId}`,
      { attachment_data: attachmentData, ...otherAttrs }
    );
  },
  toggleStandby(id) {
    return Api.put(`/tickets/${id}/toggle_standby`);
  },
  createParticipant({ id, participantId }) {
    const body = FieldNormalizer.toSnakeCase({ participantId });
    return Api.post(`/tickets/${id}/participants`, body)
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) error.errors = FieldNormalizer.toCamelCase(error.errors);
        throw error;
      });
  },
  destroyParticipant({ id, participantId }) {
    return Api.delete(`/tickets/${id}/participants?participant_id=${participantId}`)
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) error.errors = FieldNormalizer.toCamelCase(error.errors);
        throw error;
      });
  }
};

export default Tickets;
